import React, { useState } from 'react';
import { useLocation, useHistory } from 'react-router-dom';

function SwitchLoginButton() {
  const location = useLocation();
  const history = useHistory();
  const isAdminLogin = location.pathname === '/admin/login';
  return (
    <button
      type="button"
      style={{ color: isAdminLogin ? '#2d3a4b' : '#b71c1c', background: 'none', border: `1px solid ${isAdminLogin ? '#2d3a4b' : '#b71c1c'}`, borderRadius: 4, padding: '6px 16px', cursor: 'pointer' }}
      onClick={() => history.push(isAdminLogin ? '/login' : '/admin/login')}
    >
      {isAdminLogin ? 'Switch to user login' : 'ÁÆ°ÁêÜÂì°ÁôªÂÖ•'}
    </button>
  );
}

export default function Auth({ setToken, setUser }) {
  const routerLocation = useLocation();
  const [mode, setMode] = useState('login'); // or 'register'
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage('');
    try {
      if (mode === 'register') {
        const res = await fetch('/api/register', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ username, password })
        });
        const data = await res.json();
        if (res.ok) {
          setMessage('Ë®ªÂÜäÊàêÂäüÔºåË´ãÁôªÂÖ•');
          setMode('login');
        } else {
          setMessage(data.error || 'Ë®ªÂÜäÂ§±Êïó');
        }
      } else {
        const res = await fetch('/api/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ username, password })
        });
        const data = await res.json();
        if (res.ok) {
          setToken(data.token);
          setUser(username);
          localStorage.setItem('username', username);
        } else {
          setMessage(data.error || 'ÁôªÂÖ•Â§±Êïó');
        }
      }
    } catch (err) {
      setMessage('ÈÄ£Á∑öÈåØË™§');
    }
  };

  const [showPassword, setShowPassword] = useState(false);

  return (
    <div style={{ maxWidth: 350, margin: '40px auto', padding: 24, border: '1px solid #ccc', borderRadius: 8, background: '#fff', boxShadow: '0 2px 8px #eee' }}>
      <h1 style={{ textAlign: 'center', marginBottom: 18, color: '#2d3a4b', letterSpacing: 2 }}>GPT Áü•Ë≠òÁ≥ªÁµ±</h1>
      <h2 style={{ textAlign: 'center', marginBottom: 20 }}>{mode === 'login' ? 'ÁôªÂÖ•' : 'Ë®ªÂÜä'}</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <input
            type="text"
            placeholder="Â∏≥Ëôü"
            value={username}
            onChange={e => setUsername(e.target.value)}
            required
            style={{ width: '100%', marginBottom: 10, fontSize: 16, padding: 8 }}
            autoFocus
          />
        </div>
        <div style={{ position: 'relative' }}>
          <input
            type={showPassword ? 'text' : 'password'}
            placeholder="ÂØÜÁ¢º"
            value={password}
            onChange={e => setPassword(e.target.value)}
            required
            style={{ width: '100%', marginBottom: 10, fontSize: 16, padding: 8, paddingRight: 36 }}
          />
          {/* Âè™ÊúâÊ≤°Êúâ message Êó∂ÊòæÁ§∫Â∞èÁúºÁùõ */}
          {!message && (
            <span
              onClick={() => setShowPassword(v => !v)}
              style={{
                position: 'absolute',
                right: 10,
                top: '50%',
                transform: 'translateY(-50%)',
                cursor: 'pointer',
                color: '#888',
                fontSize: 18
              }}
              title={showPassword ? 'Èö±ËóèÂØÜÁ¢º' : 'È°ØÁ§∫ÂØÜÁ¢º'}
              tabIndex={0}
              onKeyDown={e => (e.key === 'Enter' || e.key === ' ') && setShowPassword(v => !v)}
            >
              {showPassword ? 'üôà' : 'üëÅÔ∏è'}
            </span>
          )}
        </div>
        <button type="submit" style={{ width: '100%', fontSize: 16, padding: 10, background: '#2d3a4b', color: '#fff', border: 'none', borderRadius: 4, marginTop: 6 }}>
          {mode === 'login' ? 'ÁôªÂÖ•' : 'Ë®ªÂÜä'}
        </button>
      </form>
      {/* Ë®ªÂÜä/ÁôªÂÖ•ÂàáÊèõÔºåÂè™Âú®‰ΩøÁî®ËÄÖÁôªÂÖ•È†ÅÈ°ØÁ§∫ */}
      {routerLocation.pathname !== '/admin/login' && (
        <div style={{ marginTop: 10, textAlign: 'center' }}>
          {mode === 'login' ? (
            <span>ÈÇÑÊ≤íÊúâÂ∏≥ËôüÔºü <button type="button" style={{ color: '#2d3a4b', background: 'none', border: 'none', cursor: 'pointer', textDecoration: 'underline' }} onClick={() => setMode('register')}>Ë®ªÂÜä</button></span>
          ) : (
            <span>Â∑≤ÊúâÂ∏≥ËôüÔºü <button type="button" style={{ color: '#2d3a4b', background: 'none', border: 'none', cursor: 'pointer', textDecoration: 'underline' }} onClick={() => setMode('login')}>ÁôªÂÖ•</button></span>
          )}
        </div>
      )}
      {/* Switch login page button */}
      <div style={{ marginTop: 24, textAlign: 'center' }}>
        <SwitchLoginButton />
      </div>
      {message && <div style={{ color: 'red', marginTop: 12, textAlign: 'center' }}>{message}</div>}
    </div>
  );
}
