import React, { useState, useEffect } from "react";
import "../assets/css/KnowledgeBase.css";
import { api } from "../config/api";

function KnowledgeBase() {
  // ÁãÄÊÖãÁÆ°ÁêÜÂçÄÂ°ä
  const [questions, setQuestions] = useState([]); // ÊâÄÊúâÂïèÈ°å
  const [filteredQuestions, setFilteredQuestions] = useState([]); // ÁØ©ÈÅ∏ÂæåÁöÑÁµêÊûú
  const [searchQuery, setSearchQuery] = useState(""); // ÊêúÂ∞ãÈóúÈçµÂ≠ó
  const [sortBy, setSortBy] = useState("likes"); // ÊéíÂ∫èÊñπÂºè
  const [selectedQuestion, setSelectedQuestion] = useState(null); // Ë¢´ÈªûÈÅ∏Êü•ÁúãË©≥ÊÉÖÁöÑÂïèÈ°å
  const [showModal, setShowModal] = useState(false); // ÊòØÂê¶È°ØÁ§∫ modal Ë©≥ÊÉÖË¶ñÁ™ó
  const [category, setCategory] = useState('ÂÖ®ÈÉ®'); // Êñ∞Â¢ûÂàÜÈ°ûÁãÄÊÖã

  // ÂèñÂæóÊâÄÊúâÂàÜÈ°ûÔºàÂèØÊ†πÊìöÂØ¶ÈöõÊÉÖÊ≥ÅË™øÊï¥‰æÜÊ∫êÔºâ
  const categories = ['ÂÖ®ÈÉ®', 'IT', '‰∫∫Ë≥á', 'Ë≤°Âãô', 'Ë°åÊîø', 'Á∏ΩÂãô', 'Ê≥ïÂãô', 'Êé°Ë≥º', 'Á†îÁôº', 'Ë°åÈä∑', 'ÂÆ¢Êúç', 'Áü•Ë≠òÁÆ°ÁêÜ'];

  // Âæû MongoDB ÂèñÂæóË≥áÊñô
  useEffect(() => {
    const fetchQuestions = async () => {
      try {
        const response = await api.get('/api/questions');
        console.log('Fetched questions:', response.data.data); 
        const fetchedQuestions = (response.data.data || []).map(q => ({
          ...q,
          likes: q.likes || 0
        }));
        setQuestions(fetchedQuestions);
        updateFilteredQuestions(fetchedQuestions, searchQuery, sortBy);
      } catch (error) {
        console.error('Error fetching questions:', error);
      }
    };

    fetchQuestions();
  }, [searchQuery, sortBy]);

  // Ê†πÊìöÊêúÂ∞ãËàáÊéíÂ∫èÊõ¥Êñ∞ÂàóË°®
  const updateFilteredQuestions = (questions, query, sort) => {
    let filtered = questions.filter(q => 
      q.question.toLowerCase().includes(query.toLowerCase())
    );

    if (sort === "likes") {
      filtered.sort((a, b) => b.likes - a.likes);
    } else if (sort === "newest") {
      filtered.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
    }

    setFilteredQuestions(filtered);
  };

  // ÊêúÂ∞ãËàáÁØ©ÈÅ∏ API
  const handleSearch = async () => {
    try {
      const params = new URLSearchParams();
      if (searchQuery) params.append('keyword', searchQuery);
      if (category && category !== 'ÂÖ®ÈÉ®') params.append('category', category);
      const res = await api.get(`/search?${params.toString()}`);
      setQuestions(res.data.data || []);
      updateFilteredQuestions(res.data.data || [], searchQuery, sortBy);
    } catch (error) {
      console.error('ÊêúÂ∞ãÂ§±Êïó:', error);
      alert('ÊêúÂ∞ãÂ§±ÊïóÔºåË´ãÁ®çÂæåÂÜçË©¶');
    }
  };

  // ÈªûÊìäÊü•ÁúãË©≥ÊÉÖ
  const handleShowDetail = (question) => {
    setSelectedQuestion(question);
    setShowModal(true);
  };

  // ËôïÁêÜÈªûËÆö
  const handleLike = async (questionId) => {
    try {
      const response = await api.post(`/api/questions/${questionId}/like`);
      if (!response.data.success) {
        throw new Error('ÈªûËÆöÂ§±Êïó');
      }
      
      // Êõ¥Êñ∞Êú¨Âú∞ÁãÄÊÖã
      const updatedQuestions = questions.map(q => 
        q._id === questionId ? { ...q, likes: q.likes + 1 } : q
      );
      setQuestions(updatedQuestions);
      updateFilteredQuestions(updatedQuestions, searchQuery, sortBy);
    } catch (error) {
      console.error('Error liking question:', error);
      alert('ÈªûËÆöÂ§±ÊïóÔºö' + (error.response?.data?.error || 'Ë´ãÁ®çÂæåÂÜçË©¶'));
    }
  };

  return (
    <div className="knowledge-base" style={{minHeight: 'calc(100vh - 140px)'}}>
      <h1>Áü•Ë≠òÂ∫´</h1>

      {/* ÊéßÂà∂ÂçÄÂ°äÔºàÊêúÂ∞ã„ÄÅÂàÜÈ°û„ÄÅÊéíÂ∫èÔºâ */}
      <div className="knowledge-controls">
        <input
          type="text"
          placeholder="ÊêúÂ∞ãÂïèÈ°å..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="search-box"
        />
        <select
          value={category}
          onChange={e => setCategory(e.target.value)}
          className="sort-dropdown"
        >
          {categories.map(cat => (
            <option key={cat} value={cat}>{cat}</option>
          ))}
        </select>
        <button onClick={handleSearch} style={{marginLeft: '8px'}}>ÊêúÂ∞ã</button>
        <select
          className="sort-dropdown"
          value={sortBy}
          onChange={(e) => setSortBy(e.target.value)}
        >
          <option value="likes">ÁÜ±ÈñÄ</option>
          <option value="newest">ÊúÄÊñ∞</option>
        </select>
      </div>

      {/* È°ØÁ§∫ÂïèÈ°åË°®Ê†º */}
      <table className="knowledge-table">
        <thead>
          <tr>
            <th>ÂïèÈ°å</th>
            <th>ÂàÜÈ°û</th>
            <th>ËÆö</th>
            <th>Êó•Êúü</th>

          </tr>
        </thead>
        <tbody>
          {filteredQuestions.map((q, index) => (
            <tr key={q._id} onClick={() => handleShowDetail(q)}>
              <td>{q.question}</td>
              <td>{q.category || 'Êú™ÂàÜÈ°û'}</td>
              <td>
                <span 
                  style={{
                    backgroundColor: '#e3f2fd',
                    border: '1px solid #e0e0e0',
                    padding: '8px 16px',
                    borderRadius: '4px',
                    cursor: 'default',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '5px'
                  }}
                >
                  üëç {q.likes}
                </span>
              </td>
              <td>{new Date(q.created_at).toLocaleDateString()}</td>

            </tr>
          ))}
        </tbody>
      </table>

      {/* Ë©≥ÊÉÖ Modal */}
      {showModal && selectedQuestion && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h2>{selectedQuestion.question}</h2>
            <p>{selectedQuestion.answer}</p>
            <div className="modal-actions">
              <button 
                onClick={() => handleLike(selectedQuestion._id)}
                style={{
                  backgroundColor: '#e3f2fd',
                  color: '#000',
                  border: 'none',
                  padding: '8px 16px',
                  borderRadius: '4px',
                  cursor: 'default',
                  marginRight: '10px',
                  pointerEvents: 'none'
                }}
              >
                üëç ÈªûËÆö ({selectedQuestion.likes})
              </button>
              <button 
                onClick={() => {
                  setShowModal(false);
                  setSelectedQuestion(null);
                }}
                style={{
                  backgroundColor: '#6c757d',
                  color: '#fff',
                  border: 'none',
                  padding: '8px 16px',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                ÈóúÈñâ
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default KnowledgeBase;