import React, { useState } from 'react';
import { useLocation, useHistory } from 'react-router-dom';

function SwitchLoginButton() {
  const location = useLocation();
  const history = useHistory();
  const isAdminLogin = location.pathname === '/admin/login';
  return (
    <button
      type="button"
      style={{ color: isAdminLogin ? '#2d3a4b' : '#b71c1c', background: 'none', border: `1px solid ${isAdminLogin ? '#2d3a4b' : '#b71c1c'}`, borderRadius: 4, padding: '6px 16px', cursor: 'pointer' }}
      onClick={() => history.push(isAdminLogin ? '/login' : '/admin/login')}
    >
      {isAdminLogin ? 'Switch to user login' : 'ÁÆ°ÁêÜÂì°ÁôªÂÖ•'}
    </button>
  );
}


export default function AdminLogin({ setAdminToken, setAdminUser }) {
  const history = useHistory();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage('');
    try {
      const res = await fetch('/api/admin-login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username, password })
      });
      const data = await res.json();
      if (res.ok) {
        setAdminToken(data.token);
        setAdminUser(username);
        setMessage('ÁôªÂÖ•ÊàêÂäüÔºåÊ≠£Âú®Ë∑≥ËΩâ...');
        history.push('/admin/dashboard');
      } else {
        if (res.status === 401) {
          setMessage('ÂÉÖÈôêÁÆ°ÁêÜÂì°ÁôªÂÖ•');
        } else {
          setMessage(data.error || 'ÁôªÂÖ•Â§±Êïó');
        }
      }
    } catch (err) {
      setMessage('ÈÄ£Á∑öÈåØË™§');
    }
  };

  return (
    <div style={{ maxWidth: 350, margin: '40px auto', padding: 24, border: '1px solid #ccc', borderRadius: 8, background: '#fff', boxShadow: '0 2px 8px #eee' }}>
      <h1 style={{ textAlign: 'center', marginBottom: 18, color: '#b71c1c', letterSpacing: 2 }}>Admin Login</h1>
      <h2 style={{ textAlign: 'center', marginBottom: 20 }}>ÁÆ°ÁêÜÂì°ÁôªÂÖ•</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <input
            type="text"
            placeholder="Â∏≥Ëôü"
            value={username}
            onChange={e => setUsername(e.target.value)}
            required
            style={{ width: '100%', marginBottom: 10, fontSize: 16, padding: 8 }}
            autoFocus
          />
        </div>
        <div>
          <input
            type="password"
            placeholder="ÂØÜÁ¢º"
            value={password}
            onChange={e => setPassword(e.target.value)}
            required
            style={{ width: '100%', marginBottom: 10, fontSize: 16, padding: 8 }}
          />
        </div>
        <button type="submit" style={{ width: '100%', fontSize: 16, padding: 10, background: '#b71c1c', color: '#fff', border: 'none', borderRadius: 4, marginTop: 6 }}>
          ÁôªÂÖ•
        </button>
      </form>
      {/* Switch login page button */}
      <div style={{ marginTop: 24, textAlign: 'center' }}>
        <SwitchLoginButton />
      </div>
      {message && <div style={{ color: 'red', marginTop: 12, textAlign: 'center' }}>{message}</div>}
    </div>
  );
}

