import React, { useEffect, useState } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  BarChart,
  Bar,
  PieChart,
  Pie,
  Cell,
} from "recharts";
import "../assets/css/DataAnalysis.css";

function DataAnalysis() {
  const [questionTrends, setQuestionTrends] = useState([]);
  const [categoryLikes, setCategoryLikes] = useState([]);
  const [popularCategories, setPopularCategories] = useState([]);
  const [activeUsers, setActiveUsers] = useState([]);
  const [timeRange, setTimeRange] = useState("monthly"); // ÊôÇÈñìÁØ©ÈÅ∏

  useEffect(() => {
    // ÂèñÂæóÁü•Ë≠òÂ∫´ÊâÄÊúâÂïèÈ°åË≥áÊñô
    fetch('/api/questions')
      .then(res => res.json())
      .then(data => {
        const questions = data.data || [];
        // ÂïèÁ≠îÈáèË∂®Âã¢Ôºà‰ª•ÊØèÊúà/ÊØèÈÄ±ÂàÜÁµÑÔºâ
        if (timeRange === "weekly") {
          // ÂÅáË®≠ created_at ÁÇ∫ ISO Â≠ó‰∏≤
          const days = ["Sun","Mon","Tue","Wed","Thu","Fri","Sat"];
          const weekTrend = Array(7).fill(0);
          questions.forEach(q => {
            const d = new Date(q.created_at);
            weekTrend[d.getDay()]++;
          });
          setQuestionTrends(days.map((date, i) => ({ date, count: weekTrend[i] })));
        } else {
          // monthly
          const months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
          const monthTrend = Array(12).fill(0);
          questions.forEach(q => {
            const d = new Date(q.created_at);
            monthTrend[d.getMonth()]++;
          });
          setQuestionTrends(months.map((date, i) => ({ date, count: monthTrend[i] })));
        }

        // ÂêÑÂàÜÈ°ûÈªûËÆöÊï∏
        const categoryLikesMap = {};
        questions.forEach(q => {
          const cat = q.category || 'Êú™ÂàÜÈ°û';
          categoryLikesMap[cat] = (categoryLikesMap[cat] || 0) + (q.likes || 0);
        });
        setCategoryLikes(Object.entries(categoryLikesMap).map(([category, likes]) => ({ category, likes }))); 

        // ÁÜ±ÈñÄÂàÜÈ°ûÔºà‰æùÂïèÈ°åÊï∏ÈáèÊéíÂ∫èÔºâ
        const categoryCountMap = {};
        questions.forEach(q => {
          const cat = q.category || 'Êú™ÂàÜÈ°û';
          categoryCountMap[cat] = (categoryCountMap[cat] || 0) + 1;
        });
        setPopularCategories(Object.entries(categoryCountMap).map(([name, value]) => ({ name, value })));

        // Ê¥ªË∫çÁî®Êà∂Ôºà‰æùÊèêÂïèÊï∏ÊéíÂ∫èÔºâ
        const userMap = {};
        questions.forEach(q => {
          // ÂèñÁî® q.usernameÔºåËã•ÁÑ°ÂâáÁî® q.user Êàñ q.uploaderÔºåÂê¶ÂâáÊ®ôÁ§∫ÁÇ∫ ''
          const user = q.username || q.user || q.uploader || '';
          if (!user) return; // Ë∑≥ÈÅéÊ≤íÊúâÁî®Êà∂ÂêçÁöÑÁ¥ÄÈåÑ
          userMap[user] = (userMap[user] || 0) + 1;
        });
        setActiveUsers(Object.entries(userMap).map(([username, count]) => ({ username, count })).sort((a, b) => b.count - a.count).slice(0, 10));
      });
  }, [timeRange]);

  const COLORS = ["#8884d8", "#82ca9d", "#ffc658", "#ff8042"];

  return (
    <div className="data-analysis">
      <h1>üìä Êï∏ÊìöÂàÜÊûê</h1>

      {/* ÊôÇÈñìÁØ©ÈÅ∏ */}
      <div className="time-filter">
        <label>ÊôÇÈñìÁØÑÂúçÔºö</label>
        <select
          value={timeRange}
          onChange={(e) => setTimeRange(e.target.value)}
        >
          <option value="weekly">Ëøë‰∏ÄÈÄ±</option>
          <option value="monthly">Ëøë‰∏ÄÊúà</option>
        </select>
      </div>

      <section>
        <div className="analysis-section">
          <h2>üü¢ ÂïèÁ≠îÈáèË∂®Âã¢</h2>
          <LineChart width={600} height={300} data={questionTrends}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="date" />
            <YAxis allowDecimals={false} />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              dataKey="count"
              stroke="#007bff"
              activeDot={{ r: 8 }}
            />
          </LineChart>
        </div>
      </section>

      <section>
        <div className="analysis-section">
          <h2>üü° ÂêÑÂàÜÈ°ûÈªûËÆöÊï∏</h2>
          <BarChart width={600} height={300} data={categoryLikes}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="category" />
            <YAxis />
            <Tooltip />
            <Bar dataKey="likes" fill="#ff8042" />
          </BarChart>
        </div>
      </section>

      <section>
        <div className="analysis-section">
          <h2>üîµ ÊúÄÂèóÊ≠°ËøéÂàÜÈ°û</h2>
          <PieChart width={400} height={300}>
            <Pie
              data={popularCategories}
              cx="50%"
              cy="50%"
              outerRadius={100}
              fill="#8884d8"
              dataKey="value"
              label
            >
              {popularCategories.map((entry, index) => (
                <Cell
                  key={`cell-${index}`}
                  fill={COLORS[index % COLORS.length]}
                />
              ))}
            </Pie>
            <Tooltip />
          </PieChart>
        </div>
      </section>

      <section>
        <div className="analysis-section">
          <h2>üë§ ‰ΩøÁî®ËÄÖÊ¥ªË∫çÊéíË°åÊ¶ú</h2>
          <table className="active-users-table">
            <thead>
              <tr>
                <th>#</th>
                <th>‰ΩøÁî®ËÄÖ</th>
                <th>ÂïèÁ≠îÊï∏</th>
              </tr>
            </thead>
            <tbody>
              {activeUsers.map((user, index) => (
                <tr key={user.username}>
                  <td>{index + 1}</td>
                  <td>{user.username}</td>
                  <td>{user.count}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </section>
    </div>
  );
}

export default DataAnalysis;
