import React, { useState } from "react";
import api from '../config/api';

function Project() {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [likes, setLikes] = useState(0);
  const [loading, setLoading] = useState(false);
  const [hasLiked, setHasLiked] = useState(false);
  const [error, setError] = useState(null);
  const [questionId, setQuestionId] = useState(null);
  const [isSaved, setIsSaved] = useState(false);
  const [category, setCategory] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!question.trim()) {
      alert('Ë´ãËº∏ÂÖ•ÂïèÈ°å');
      return;
    }

    setLoading(true);
    setError('');
    setAnswer('');
    setIsSaved(false);
    setLikes(0);
    setHasLiked(false);
    try {
      // ÂèñÂæó usernameÔºåËã•ÁÑ°ÂâáÈ†êË®≠ anonymous
      let username = localStorage.getItem('username') || 'anonymous';
      const response = await api.post('/ask', { question, username });
      setAnswer(response.data.answer || '');
      setCategory(response.data.category || ''); // ÂÑ≤Â≠òÂàÜÈ°û
      setQuestionId(response.data.questionId);
      if (response.data.likes !== undefined) setLikes(response.data.likes);
      // Ëá™ÂãïË®≠ÁÇ∫Â∑≤Êî∂ËóèÔºàË≥áÊñôÂ∫´Â∑≤ÊúâÁõ∏ÂêåÂïèÈ°åÊôÇÔºâ
      if (response.data.isCollected) {
        setIsSaved(true);
      } else {
        setIsSaved(false);
      }
      if (!response.data.questionId) throw new Error('Êú™Êî∂Âà∞ÂïèÈ°å ID');
      localStorage.setItem('currentQuestionId', response.data.questionId);
      setError(null);
      setQuestion(""); // ÊèêÂïèÊàêÂäüÂæåÊ∏ÖÁ©∫Ëº∏ÂÖ•Ê°Ü
    } catch (error) {
      console.error('Error:', error);
      if (error.message !== 'Êú™Êî∂Âà∞ÂïèÈ°å ID') {
        setError(error.message || 'ÁôºÁîüÈåØË™§ÔºöË´ãÁ®çÂæåÂÜçË©¶');
        alert(error.message || 'ÁôºÁîüÈåØË™§ÔºöË´ãÁ®çÂæåÂÜçË©¶');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleLike = async () => {
    try {
      const storedId = questionId || localStorage.getItem('currentQuestionId');
      if (!storedId) {
        setLikes(likes + 1);
        setHasLiked(true);
        alert('Ê≠§ÂïèÈ°åÂ∞öÊú™Êî∂ËóèÔºåÈªûËÆöÂÉÖÊö´Â≠òÊñºÊú¨Âú∞');
        return;
      }
      const response = await api.post(`/api/questions/${storedId}/like`);
      if (response.data.success) {
        setLikes(likes + 1);
        setHasLiked(true);
        setError(null);
      } else {
        throw new Error(response.data.error || 'ÈªûËÆöÂ§±Êïó');
      }
    } catch (error) {
      console.error('Error liking:', error);
      setError(error.message || 'ÈªûËÆöÂ§±ÊïóÔºöË´ãÁ®çÂæåÂÜçË©¶');
      alert(error.message || 'ÈªûËÆöÂ§±ÊïóÔºöË´ãÁ®çÂæåÂÜçË©¶');
    }
  };


  const handleSave = async () => {
    try {
      if (!answer) throw new Error('Ê≤íÊúâÂèØÊî∂ËóèÁöÑÁ≠îÊ°à');
      if (isSaved) {
        alert('Ê≠§ÂïèÈ°åÂ∑≤Êî∂ËóèÂà∞Êï∏ÊìöÂ∫´');
        return;
      }
      const response = await api.post('/collect', {
        question,
        answer,
        category, // Êî∂ËóèÊôÇÂ∏∂‰∏äÂàÜÈ°û
        likes // ÂÇ≥ÈÄÅÁõÆÂâçÁöÑÈªûËÆöÊï∏
      });
      if (response.data && !response.data.error) {
        setIsSaved(true);
        // --- Auto-refresh knowledge base page ---
        // If user is on /knowledge, reload the page. Otherwise, navigate to /knowledge.
        if (window.location.pathname === '/knowledge') {
          window.location.reload();
        } else {
          window.location.href = '/knowledge';
        }
      } else {
        throw new Error(response.data?.error || 'Êî∂ËóèÂ§±Êïó');
      }
    } catch (error) {
      console.error('Error saving:', error);
      setError(error.message || 'Êî∂ËóèÂ§±ÊïóÔºöË´ãÁ®çÂæåÂÜçË©¶');
      alert(error.message || 'Êî∂ËóèÂ§±ÊïóÔºöË´ãÁ®çÂæåÂÜçË©¶');
    }
  };

  return (
    <div className="App">
      <h1>AI Áü•Ë≠òÂ∫´ÂïèÁ≠îÁ≥ªÁµ±</h1>

      <form onSubmit={handleSubmit} className="question-box">
        <textarea
          placeholder="Ë´ãËº∏ÂÖ•ÊÇ®ÁöÑÂïèÈ°å..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          disabled={loading}
         
        />
        <button type="submit" disabled={loading}>
          {loading ? 'Ê≠£Âú®ÊÄùËÄÉ...' : '‚ú® ÊèêÂïè'}
        </button>
      </form>

      {answer && (
        <div className="answer-box">
          <h2>‚ú® AI ÂõûÁ≠îÔºö</h2>
          <p>{answer}</p>
          <div style={{ display: 'flex', gap: '10px', marginTop: '15px' }}>
            <button 
              onClick={() => {
                if (!isSaved) {
                  setLikes(likes + 1);
                  setHasLiked(true);
                  alert('Ê≠§ÂïèÈ°åÂ∞öÊú™Êî∂ËóèÔºåÈªûËÆöÂÉÖÊö´Â≠òÊñºÊú¨Âú∞');
                  return;
                }
                handleLike();
              }}
              disabled={hasLiked}
              title={!isSaved ? 'Ê≠§ÂïèÈ°åÂ∞öÊú™Êî∂ËóèÔºåÈªûËÆöÂÉÖÊö´Â≠òÊñºÊú¨Âú∞' : ''}
            >
              {hasLiked ? 'Â∑≤ÈªûËÆö' : `üëç ÈªûËÆö (${likes})`}
            </button>
            <button onClick={handleSave} disabled={isSaved || !answer}>
              {isSaved ? 'Â∑≤Êî∂Ëóè' : '‚ú® Êî∂ËóèÂà∞Êï∏ÊìöÂ∫´'}
            </button>
          </div>
        </div>
      )}

      {error && (
        <div style={{ color: 'red', marginTop: '20px' }}>
          {error === 'Êú™Êî∂Âà∞ÂïèÈ°å ID' ? null : error}
        </div>
      )}
    </div>
  );
}

export default Project;
