import React, { useEffect, useState } from 'react';

export default function AdminQA({ adminToken }) {
  const [questions, setQuestions] = useState([]);
  const [message, setMessage] = useState('');
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    fetch('/api/questions', {
      headers: { Authorization: `Bearer ${adminToken}` }
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) setQuestions(data.questions || data.data || []);
        else setMessage(data.error || 'ËÆÄÂèñÂ§±Êïó');
      })
      .catch(() => setMessage('ÈÄ£Á∑öÈåØË™§'));
  }, [adminToken]);

  const handleSearch = async () => {
    try {
      const params = new URLSearchParams();
      if (searchQuery) params.append('keyword', searchQuery);
      const res = await fetch(`/search?${params.toString()}`, {
        headers: { Authorization: `Bearer ${adminToken}` }
      });
      const data = await res.json();
      if (data.success) setQuestions(data.data || []);
      else setMessage(data.error || 'ÊêúÂ∞ãÂ§±Êïó');
    } catch {
      setMessage('ÊêúÂ∞ãÂ§±Êïó');
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm('Á¢∫ÂÆöË¶ÅÂà™Èô§ÈÄôÂÄãÂïèÈ°åÂóéÔºü')) return;
    try {
      const res = await fetch(`/api/questions/${id}`, {
        method: 'DELETE',
        headers: { Authorization: `Bearer ${adminToken}` }
      });
      const data = await res.json();
      if (data.success) setQuestions(questions.filter(q => q._id !== id));
      else setMessage(data.error || 'Âà™Èô§Â§±Êïó');
    } catch {
      setMessage('ÈÄ£Á∑öÈåØË™§');
    }
  };

  return (
    <div className="knowledge-base" style={{minHeight: 'calc(100vh - 140px)'}}>
      <h1>ÂïèÁ≠îÁÆ°ÁêÜ</h1>
      {message && <div style={{ color: 'red' }}>{message}</div>}
      {/* ÊéßÂà∂ÂçÄÂ°äÔºàÊêúÂ∞ã„ÄÅÂàÜÈ°ûÔºâ */}
      <div className="knowledge-controls">
        <input
          type="text"
          placeholder="ÊêúÂ∞ãÂïèÈ°å..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="search-box"
        />
        <button onClick={handleSearch} style={{marginLeft: '8px'}}>ÊêúÂ∞ã</button>
      </div>
      <table className="knowledge-table">
        <thead>
          <tr>
            <th>ÂïèÈ°å</th>
            <th>ÂàÜÈ°û</th>
            <th>ËÆö</th>
            <th>Êó•Êúü</th>
            <th>Êìç‰Ωú</th>
          </tr>
        </thead>
        <tbody>
          {questions.map(q => (
            <tr key={q._id}>
              <td>{q.question}</td>
              <td>{q.category || 'Êú™ÂàÜÈ°û'}</td>
              <td>üëç {q.likes || 0}</td>
              <td>{q.created_at ? new Date(q.created_at).toLocaleDateString() : ''}</td>
              <td>
                <button 
                  onClick={() => handleDelete(q._id)}
                  className="delete-button"
                  style={{
                    backgroundColor: '#dc3545',
                    color: '#fff',
                    border: 'none',
                    padding: '8px 16px',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    fontSize: '14px'
                  }}
                >
                  ‚úñÔ∏è Âà™Èô§
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
