import React, { useState, useEffect } from "react";
import "../assets/css/DocumentManagement.css";

function DocumentManagement() {
  const [files, setFiles] = useState([]);
  const [uploading, setUploading] = useState(false);

  // ÂèñÂæóÂ∑≤‰∏äÂÇ≥Ê™îÊ°àÂàóË°®
  const fetchFiles = async () => {
    try {
      const res = await fetch('/api/files');
      const data = await res.json();
      if (data.success) setFiles(data.files);
      else setFiles([]);
    } catch {
      setFiles([]);
    }
  };

  useEffect(() => {
    fetchFiles();
  }, []);

  // ‰∏äÂÇ≥Ê™îÊ°à
  const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;
    setUploading(true);
    const formData = new FormData();
    formData.append('file', file);
    // ‰∏çÂÜçÊâãÂãïÈôÑÂä† uploaderÔºåÊîπÁî±ÂæåÁ´ØÂæû JWT ÂèñÂæó
    try {
      const token = localStorage.getItem('token'); // ÂÅáË®≠ÁôªÂÖ•ÊôÇÂ∑≤Â≠òÂÖ• localStorage
      const res = await fetch('/api/upload', {
        method: 'POST',
        headers: token ? { 'Authorization': 'Bearer ' + token } : {},
        body: formData
      });
      const data = await res.json();
      if (data.success) {
        fetchFiles();
        alert('‰∏äÂÇ≥ÊàêÂäü');
      } else {
        alert(data.error || '‰∏äÂÇ≥Â§±Êïó');
      }
    } catch {
      alert('‰∏äÂÇ≥Â§±Êïó');
    }
    setUploading(false);
  };

  return (
    <div className="document-management">
      <h1>ÊñáÊ™îÁÆ°ÁêÜ</h1>
      <div className="upload-section">
        <input
          type="file"
          onChange={handleFileUpload}
          className="file-input"
          disabled={uploading}
        />
      </div>
      <div className="file-list">
        <h2>Â∑≤‰∏äÂÇ≥Êñá‰ª∂</h2>
        <ul>
          {files.map((file, index) => (
            <li key={index} className="file-item">
              <span className="file-name">{file.filename}</span>
              <span style={{ marginLeft: 12, color: '#888', fontSize: 14 }}>
                ‰∏äÂÇ≥ËÄÖÔºö{file.uploader || 'Êú™Áü•'}
              </span>
              <span style={{ marginLeft: 12, color: '#888', fontSize: 14 }}>
                {file.upload_time ? new Date(file.upload_time).toLocaleString() : ''}
              </span>
              <a
                className="download-btn"
                href={`/api/download/${encodeURIComponent(file.filename)}`}
                download={file.filename}
                style={{ marginLeft: 12 }}
              >
                ‰∏ãËºâ
              </a>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default DocumentManagement;
